// 640px, 1024px, 1400px
$breakpoints-up: (
  "small": "576px",
  "medium": "768px",
  "large": "992px",
  "xlarge": "1200px",
  "xxlarge": "1400px"
);

// 639px, 1023px, 1399px
$breakpoints-down: (
  "small": "39.9375em",
  "medium": "63.9375em",
  "large": "87.4375em"
);

@mixin breakpoint-up($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
